// src/store/index.js
import { createStore } from 'vuex';

export default createStore({
  state() {
    return {
      variable: 'Servicio'
    };
  },
  mutations: {
    setVariable(state, newValue) {
      state.variable = newValue;
    }
  },
  actions: {
    updateVariable({ commit }, newValue) {
      commit('setVariable', newValue);
    }
  }
});
