<template>
    <div class="products container  mx-auto px-4 py-16">
      <h1 class="text-3xl font-bold text-center mb-8">Catálogo de Productos</h1>
      <!-- Buscador -->
      <div class="flex justify-center mb-8">
        <input 
          v-model="searchQuery" 
          type="text" 
          placeholder="Buscar productos..." 
          class="w-full md:w-1/2 p-3 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500"
        />
      </div>
      <!-- Lista de Productos -->
      <div class="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
        <div 
          v-for="product in filteredProducts" 
          :key="product.id" 
          class="product-card bg-white rounded-lg shadow-md p-6"
        >
          <img 
            :src="product.image" 
            alt="Product Image" 
            class="product-image w-full h-48 object-cover mb-4 rounded-lg" 
          />
          <h2 class="product-title text-xl font-semibold mb-2">{{ product.name }}</h2>
          <p class="product-description text-gray-600 mb-4">{{ product.description }}</p>
          <div class="flex justify-between items-center">
            <span class="product-price text-lg font-bold text-blue-600">${{ product.price }}</span>
            <button 
              class="add-to-cart-button bg-blue-600 text-white font-semibold px-4 py-2 rounded-lg hover:bg-blue-700 focus:ring-2 focus:ring-blue-500"
            >
              Agregar al Carrito
            </button>
          </div>
        </div>
      </div>
    </div>
  </template>
  
  <script>
  export default {
    name: "ProductsPage",
    data() {
      return {
        searchQuery: "", // Para almacenar el término de búsqueda
        products: [
          {
            id: 1,
            name: "Pastillas de Freno",
            description: "Descripción del Producto 1",
            price: 10.99,
            image: "path/to/product1.jpg",
          },
          {
            id: 2,
            name: "Bujias",
            description: "Descripción del Producto 2",
            price: 19.99,
            image: "path/to/product2.jpg",
          },
          {
            id: 3,
            name: "Solenoide",
            description: "Descripción del Producto 2",
            price: 19.99,
            image: "path/to/product2.jpg",
          }
          // Agrega más productos aquí
        ],
      };
    },
    computed: {
      filteredProducts() {
        return this.products.filter(product => {
          const searchTerm = this.searchQuery.toLowerCase();
          return (
            product.name.toLowerCase().includes(searchTerm) ||
            product.description.toLowerCase().includes(searchTerm)
          );
        });
      },
    },
  };
  </script>
  
  <style scoped>
  .products {
    max-width: 1200px;
  }
  </style>
  